
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'

import UtilMixin from '@/mixins/UtilMixin.vue'

export default {
mixins: [UtilMixin],
data() {
return {
customer_id: null

}
},
components: {
Marquee,
MarqueeItem,
Flexbox,
FlexboxItem,
Divider
},
computed: {},
methods: {
applyForLoan() {
let _this = this
if (this.customer_id) {
this.routerLink('ApplyForLoan')
} else {
this.$vux.confirm.show({
title: '提示',
content: '您还没有登录,请先登录!',
onCancel() {},
onConfirm() {
_this.routerLink('Login', {identification: '000'})
}
})
}
}
},
created() {
this.customer_id = localStorage.getItem('yunhu!customer_id')
},
mounted() {

}
}



